      <BreadcrumbItem>{{mbx[1]}}</BreadcrumbItem>
      <BreadcrumbItem>{{mbx[2]}}</BreadcrumbItem>
    </Breadcrumb>
    <Content :style="{padding: '24px', minHeight: '280px', background: '#fff'}">
      <slot></slot>
    </Content>
  </div>
</template>

<script>
  export default {
    name: "inBody",
    props:{
      mbx:Array
    }
  }
</script>

